body, .tracking-container {
  font-family: 'Inter', sans-serif;
  background: #F5F5F5;
  color: #2E1065;
}

.tracking-container {
  width: 100%;
  min-height: 100vh;
  padding: 1.2rem 0;
  background: #F5F5F5;
  color: #2E1065;
}

.tracking-header {
  text-align: center;
  margin-bottom: 1.5rem;
  background: none;
  opacity: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.3rem;
}

.tracking-header h1 {
  display: inline-block;
  font-family: 'Inter', sans-serif;
  font-weight: 900;
  font-size: 40px;
  line-height: 1;
  color: #B5A2D8;
  text-transform: uppercase;
  margin: 0;
  position: relative;
}
.tracking-header h1::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 160px;
  height: 4px;
  background: linear-gradient(90deg, rgba(181,162,216,0) 0%, rgba(181,162,216,1) 50%, rgba(181,162,216,0) 100%);
}
.tracking-header p {
  font-size: 13px;
  color: #2E1065;
  opacity: 0.7;
  margin: 1rem 0 0.2rem 0;
}

.tracking-container h2 {
  font-family: 'Inter', sans-serif;
  font-weight: 900;
  font-size: 28px;
  color: #B5A2D8;
  margin: 0 0 1.5rem 0;
  text-transform: none;
}

.chart-section, .table-section {
  max-width: 1100px;
  margin: 1.2rem auto 1.2rem;
  padding: 0.7rem 1rem;
  background: rgba(199,183,227,0.15); /* #C7B7E3 opacité 15% */
  border-radius: 15px;
  box-shadow: 0 4px 12px rgba(181,162,216,0.35);
  border: none;
}

.chart-title, .table-header h2 {
  font-size: 18px;
  font-weight: 700;
  color: #2E1065;
  text-transform: uppercase;
  margin-bottom: 0.2rem;
  background: none;
  -webkit-background-clip: unset;
  -webkit-text-fill-color: unset;
}

.chart-title + p, .table-header p {
  color: #2E1065;
  font-size: 12px;
  opacity: 0.7;
  margin-bottom: 0.5rem;
}

.count-pill {
  background: linear-gradient(90deg, #B5A2D8 0%, #7F6F9D 100%);
  border-radius: 12px;
  color: #fff;
  font-weight: 700;
  font-size: 12px;
  border: none;
  padding: 4px 12px;
  margin-bottom: 0.5rem;
  box-shadow: 0 2px 8px rgba(181,162,216,0.10);
}

.table-wrapper {
  background: transparent;
  border-radius: 15px;
  box-shadow: none;
  border: none;
  margin-bottom: 1.2rem;
  overflow-x: auto !important;
}

.table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  background: transparent;
}

.table thead th {
  background: #ECE6F0;
  color: #2E1065;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 12px;
  padding: 0.7rem 0.8rem;
  border-bottom: 2px solid #B5A2D8;
}

.table tbody tr {
  background: rgba(181,162,216,0.07);
  border-bottom: 1px solid #ECE6F0;
  transition: background 0.2s;
}

.table tbody tr:hover {
  background: #E4E1EA;
}

.table th, .table td {
  padding: 0.7rem 0.8rem;
  color: #2E1065;
  font-size: 12px;
  font-weight: 500;
  white-space: normal;
  word-break: break-word;
  overflow-wrap: break-word;
}

.status-cell {
  font-size: 1rem;
  color: #FF3F52;
  font-weight: bold;
}

.param-badge, .table td span[style*="background"] {
  display: inline-flex;
  align-items: center;
  padding: 0.15rem 0.5rem;
  margin: 0.1rem;
  border-radius: 12px;
  font-weight: 600;
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background: #ECE6F0;
  color: #9B6F9D;
  border: 1px solid #ACA0C3;
  box-shadow: 0 0 6px rgba(181,162,216,0.08);
}

.param-badge.light {
  background: #ACA0C3;
  color: #fff;
}

/* Pills, sélecteurs, boutons arrondis */
select, .per-page-selector select {
  border-radius: 12px;
  border: 1px solid #D9D1E3;
  background: #ECE6F0;
  color: #2E1065;
  font-size: 12px;
  padding: 4px 8px;
  margin-left: 8px;
}

/* Pagination boutons */
.anomaly-pager-btn {
  background: #ECE6F0;
  border: none;
  border-radius: 12px;
  color: #2E1065;
  margin: 0 2px;
  padding: 2px 6px;
  transition: background 0.2s;
}
.anomaly-pager-btn:disabled {
  opacity: 0.4;
}
.anomaly-pager-btn:hover:not(:disabled) {
  background: #B5A2D8;
  color: #fff;
}

/* Responsive */
@media (max-width: 1200px) {
  .tracking-header h1 { font-size: 1.2rem; }
  .chart-title, .table-header h2 { font-size: 1rem; }
  .table { font-size: 0.95rem; }
}
@media (max-width: 768px) {
  .tracking-container { padding: 0.5rem 0; }
  .chart-section, .table-section { padding: 0.5rem; }
  .tracking-header h1 { font-size: 1rem; }
  .chart-title, .table-header h2 { font-size: 0.9rem; }
  .chart-wrapper, .table-wrapper { padding: 0.5rem; border-radius: 10px; }
  .table th, .table td { padding: 0.4rem; font-size: 0.8rem; }
  .table { font-size: 0.8rem; }
  .table, .table-wrapper { min-width: unset; }
  .param-badge { font-size: 0.6rem; padding: 0.1rem 0.3rem; }
}
@media (max-width: 480px) {
  .tracking-header h1 { font-size: 0.8rem; }
  .chart-title, .table-header h2 { font-size: 0.7rem; }
  .table th, .table td { padding: 0.2rem 0.1rem; font-size: 0.7rem; }
  .table { font-size: 0.7rem; }
  .table, .table-wrapper { min-width: unset; }
}
@media (max-width: 600px) {
  .log-controls {
    flex-direction: column !important;
    align-items: stretch !important;
    gap: 8px !important;
  }
  .log-controls .page-info,
  .log-controls .per-page-selector {
    font-size: 11px !important;
    padding: 0.2rem 0.5rem !important;
    border-radius: 12px !important;
    margin-right: 0 !important;
    margin-bottom: 4px !important;
  }
  .pagination-buttons {
    justify-content: center !important;
    gap: 4px !important;
  }
  .anomaly-pager-btn {
    min-width: 28px !important;
    min-height: 28px !important;
    font-size: 14px !important;
    padding: 0.1rem 0.3rem !important;
  }
  .table-section, .chart-section {
    padding: 0.3rem 0.2rem !important;
    border-radius: 8px !important;
  }
  .tracking-header h1 {
    font-size: 1.1rem !important;
  }
  .table-wrapper, .events-detail-table-wrapper {
    overflow-x: auto !important;
    padding: 0 !important;
    border-radius: 8px !important;
  }
  .table th, .table td {
    font-size: 10px !important;
    padding: 0.2rem 0.3rem !important;
  }
  .param-badge {
    font-size: 0.5rem !important;
    padding: 0.05rem 0.2rem !important;
    border-radius: 8px !important;
  }
}

/* Effets d'entrée (optionnel) */
.chart-wrapper, .table-wrapper {
  animation: slideInUp 0.8s ease-out forwards;
  opacity: 1;
  transform: none;
}
@keyframes slideInUp {
  to { opacity: 1; transform: translateY(0); }
}

.table-wrapper::-webkit-scrollbar {
  height: 10px;
}
.table-wrapper::-webkit-scrollbar-thumb {
  background: #B5A2D8;
  border-radius: 8px;
}
.table-wrapper::-webkit-scrollbar-track {
  background: #ECE6F0;
  border-radius: 8px;
}

.tracking-plan-table {
  margin-left: -40px;
  min-width: 1200px;
}
.tracking-plan-table th, .tracking-plan-table td {
  font-size: 11px !important;
  padding: 0.3rem 0.5rem !important;
  min-width: 80px;
  max-width: 120px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
@media (max-width: 768px) {
  .tracking-plan-table th, .tracking-plan-table td {
    font-size: 10px !important;
  }
}
@media (max-width: 480px) {
  .tracking-plan-table th, .tracking-plan-table td {
    font-size: 9px !important;
  }
}

.events-detail-table-wrapper {
  overflow-x: auto;
}
.events-detail-table {
  min-width: 1400px;
  width: auto;
}
