.events-detail-table th:nth-child(4), .events-detail-table td:nth-child(4) { min-width: 110px; }
.events-detail-table th:nth-child(5), .events-detail-table td:nth-child(5) { min-width: 120px; }
.events-detail-table th:nth-child(6), .events-detail-table td:nth-child(6) { min-width: 120px; }
.events-detail-table th:nth-child(7), .events-detail-table td:nth-child(7) { min-width: 170px; }
.events-detail-table th:nth-child(8), .events-detail-table td:nth-child(8) { min-width: 170px; }
.events-detail-table th:nth-child(9), .events-detail-table td:nth-child(9) { min-width: 170px; }
.events-detail-table th:nth-child(10), .events-detail-table td:nth-child(10) { min-width: 170px; }
.events-detail-table th:nth-child(11), .events-detail-table td:nth-child(11) { min-width: 80px; }
.events-detail-table th:nth-child(12), .events-detail-table td:nth-child(12) { min-width: 200px; }
.events-detail-table td {
  /* overflow: hidden; */
