}
.events-detail-table th {
  background: #ECE6F0 !important;
  border-bottom: 2px solid #B5A2D8 !important;
  font-weight: 700;
  text-transform: uppercase;
  white-space: normal;
  word-break: break-word;
  min-width: unset;
}
.events-detail-table td {
  font-weight: 500;
  border-bottom: 1px dashed rgba(155,111,157,0.5);
  min-width: 120px;
}
.events-detail-table tr:last-child td {
  border-bottom: none;
}
.events-detail-table th:first-child {
